<template lang="html">
  <div class="wrap">
<van-nav-bar
  title="商品分类"
  left-text=""
  left-arrow
  @click-left="$router.go(-1)"
/>
    <section class="view">
      <v-aside :datas="allData"></v-aside>
      <router-view :datas="allData"></router-view>
    </section>

    <v-baseline></v-baseline>
    <v-footer></v-footer>

  </div>
</template>

<script>

import Header from '@/common/_header.vue'
import Aside from '@/components/category/aside.vue'
import Baseline from '@/common/_baseline.vue'
import Footer from '@/common/_footer.vue'
import { NavBar,Dialog } from 'vant';
import 'vant/lib/vant-css/index.css';

export default {
  components:{
    [NavBar.name]:NavBar,
    'v-aside': Aside,
    'v-baseline': Baseline,
    'v-footer': Footer
  },
  data(){
    return {
      allData:''
    }
  },
  created(){
	  this.$dopost('/sysapi/pro/sort/', null,
		function (res) {
			if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0)
			{
        		this.allData = res.data.data;
			}
			else{Toast((res.data && res.data.mess)?res.data.mess:'接口错误');}
    }.bind(this));
  }
}
</script>

<style lang="less" scoped>
  .wrap {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column nowrap;
            flex-flow: column nowrap;

    .view {
      width: 100%;
      height:100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
    }
  }
</style>



